    max-width: 1150px;
  }
}

.h1 {
  font-size: var(--fs-1);
  font-weight: 400;
  line-height: 1.2;
}

.h2, .h3, .h4 {
  font-family: var(--ff-fira);
}

.h2 {
  font-size: var(--fs-2);
}

.h3 {
  font-size: var(--fs-3);
}

.h4 {
  font-size: var(--fs-5);
}

.w-100 {
  width: 100%;
}

.btn {
  max-width: max-content;
  color: var(--color-primary);
  font-weight: var(--fw-700);
  text-transform: uppercase;
  letter-spacing: .5px;
  border-radius: var(--radius-4);
  transition: var(--transition-1);
  border: 1px solid rgba(0, 0, 0, 0);
  padding: 10px 25px;
}

.btn-primary {
  border-color: var(--color-primary);
}

.btn-primary:-webkit-any(:hover, :focus) {
  background: var(--color-primary);
  color: var(--bg-primary);
}

.btn-primary:is(:hover, :focus) {
  background: var(--color-primary);
  color: var(--bg-primary);
}

.btn-secondary {
  background: var(--raw-seinna);
  border-color: var(--raw-seinna);
  color: var(--black);
}

.btn-secondary:-webkit-any(:hover, :focus) {
  --raw-seinna: #e7965f;
}

.btn-secondary:is(:hover, :focus) {
  --raw-seinna: #e7965f;
}

.section-subtitle {
  color: var(--blue);
  text-transform: uppercase;
  margin-bottom: 20px;
  padding-bottom: 5px;
  position: relative;
}

@media screen and (min-width: 992px) {
  .section-subtitle {
    transform-origin: 0 0;
    margin-bottom: 0;
    position: absolute;
    top: 0;
    left: 0;
    transform: rotate(.75turn)translateX(-100%);
  }
}

.section-subtitle:after {
  content: "";
  width: 60px;
  height: 2px;
  background: var(--color-secondary);
  position: absolute;
  top: 100%;
  left: 0;
}

@media screen and (min-width: 992px) {
  .section-subtitle:after {
    top: 8px;
    left: auto;
    right: calc(100% + 20px);
  }
}

.section-title {
